.pagination {
  font-size: 13px;
  margin-top: 5px;
  &__title {
    color: @lGrey;
  }
  ul {
    display: inline-block;
    margin-right: 5px;
  }
  li {
    display: inline-block;
  }
  svg {
    font-size: 2.4em;
    vertical-align: middle;
  }
  li {
    color: @lGrey;
  }
  a {
    color: @grey;
  }
  &__item {
    height: 31px;
  }
  &__next {
    transform: rotate(180deg);
  }
  &__view {
    vertical-align: 1px;
    margin-top: 7px;
    margin-left: 10px;
    svg {
      font-size: 1em;
      fill: @grey;
    }
    &_active {
      svg {
        fill: @orange;
      }
    }
  }
}
