.popular {
  padding-top: 25px;
  &__slider {
    position: relative;
    margin-top: 30px;
    .owl-nav {
      position: absolute;
      top: 120px;
      width: 100%;
      z-index: 100;
      svg {
        fill: @lGrey;
        font-size: 2.6em;
      }
    }
    .owl-prev {
      position: absolute;
      left: -50px;
    }
    .owl-next {
      position: absolute;
      right: -50px;
    }
  }
}