.goods-tile {
  margin-top: 68px;
  &__item {
    position: relative;
    color: @grey;
    margin-bottom: 28px;
  }
  &__wrap {
    padding: 25px 21px;
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
  }
  &__title {
    text-transform: uppercase;
    font-size: 22px;
    margin-bottom: 16px;
    width: 200px;
    line-height: 28px;
    &_new {
      color: @green;
    }
    &_sales{
      color: @red;
    }
  }
  &__descript {
    width: 210px;
    font-size: 16px;
    line-height: 21px;
  }
  .btn {
    position: absolute;
    left: 21px;
    bottom: 32px;
  }
}