.footer {
  background-color: @xxlGrey;
  padding: 36px 0;
  margin-top: 55px;
  padding-bottom: 60px;
  &__icon {
    position: relative;
    border: solid 2px #fff;
    height: 60px;
    padding-top: 10px;
    vertical-align: middle;
    line-height: 30px;
    margin: 5px 0 9px;
    padding-left: 48px;
    padding-right: 7px;
    svg {
      // display: inline-block;
      position: absolute;
      left: 10px;
      top: 12px;
      fill: @lGrey;
      font-size: 1.8em;
      // vertical-align: top;
      // margin-left: 5px;
      // margin-right: 5px;
      &.icon-point {
        left: 13px;
      }
    }
    .link {
      line-height: 17px;
      display: inline-block;
      font-size: 13px;
      width: 80px;
    }
  }
  &__links{
    margin-top: 10px;
    ul {
      padding-top: 12px;
      line-height: 20px;
      float: left;
    }
    li {
      font-size: 12px;
    }
  }
  &__service {
    margin-left: 52px;
    padding-right: 54px;
    border-right: solid 1px #fff;
    ul:first-of-type {
      margin-right: 100px;
    }
  }
  &__about {
    margin-left: 25px;
    padding-right: 55px;
    border-right: solid 1px #fff;
    ul:first-of-type {
      margin-right: 80px;
    }
  }
  &__title {
    font-size: 15px;
    color: @grey;
  }
  &__social {
    margin-top: 10px;
    margin-left: 30px;
    width: 151px;
    ul {
      margin: 15px -2px;
    }
    li {
      display: inline-block;
      margin: 0 2px;
      line-height: 32px;
    }
    svg {
      font-size: 1.9em;
    }
  }
  &__rights {
    margin-top: 21px;
    padding-top: 22px;
    line-height: 20px;
    border-top: solid 1px #fff;
    font-size: 12px;
    p {
      width: 390px;
    }
  }
}
