.catalog {
  margin-top: 28px;
  margin-bottom: 20px;
  font-size: 14px;
  &__image {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 255px;
  }
  &__title {
    display: block;
    margin-top: 16px;
    margin-bottom: 3px;
    font-weight: bold;
    &_link {
      display: block;
      text-decoration: underline;
    }
    &_l {
      font-size: 20px;
    }
  }
  &__description {
    min-height: 38px;
    display: block;
    color: @lGrey;
    line-height: 19px;
    margin-bottom: 8px;
  }
  &__price {
    color: @orange;
  }
  &__label {
    background-color: @orange;
    border-radius: 3px;
    font-size: 12px;
    color: #fff;
    padding: 2px 7px;
    height: 19px;
  }
}