.header {
  margin-top: 14px;
  color: @grey;
  &__top {
    border-bottom: solid 2px @orange;
    padding-bottom: 16px;
  }
  .icon {
    fill: @lGrey;
  }
  &__form {
    margin-left: 55px;
    width: 620px;
  }
  &__search {
    position: relative;
    height: 40px;
    font-size: 16px;
    color: @grey;
    input {
      font-size: 16px;
      padding-left: 16px;
      letter-spacing: -0.04em;
    }
    .icon {
      position: absolute;
      top: 11px;
      right: 8px;
      font-size: 1.2em;
    }
  }
  &__user {
    margin-top: 19px;
  }
  &__reg {
    // line-height: 12px;
  }
  &__cart {
    text-align: right;
    font-size: 13px;
    margin-top: 13px;
    .icon {
      vertical-align: -2px;
      font-size: 1.3em;
      margin-right: 4px;
    }
  }
  &__nav {
    text-align: justify;
    margin: 10px -30px 0;
    position: relative;
    font-size: 14px;
    &:before {
      width: 52px;
      height: 100%;
      content: '';
      background-color: #fff;
      right: -22px;
      position: absolute;
      top: 0;
      z-index: 10;
    }
    &__sub {
      border: solid 1px @xlGrey;
      position: absolute;
      background-color: #fff;
      z-index: 100;
      white-space: nowrap;
      top: 460%;
      padding: 0;
      color: @grey;
      -webkit-transition: opacity 0.3s linear;
      transition: opacity 0.3s linear;
      -webkit-transform-origin: 50% 0;
      transform-origin: 50% 0;
      overflow: hidden;
      pointer-events: none;
      opacity: 0;
      margin: -1px;
      height: 0;
      &:hover {
        color: @grey;
      }
      &_left {
        left: 11px;
      }
      &_right {
        right: -13px;
      }
      li {
        display: inline-block;
        text-align: left;
        text-transform: none;
        padding: 0 10px;
        > ul li {
          display: block;
          padding: 0;
          margin-top: 16px;
        }
      }
    }
    &:after {
      content: '';
      display: inline-block;
      width: 100%;
    }
    &__link {
      position: relative;
      display: block;
      padding: 0 30px;
    }
    &__item {
      display: inline-block;
      text-transform: uppercase;
      position: relative;
      margin-top: 12px;
      &:before {
        -webkit-transition: opacity 0.2s linear;
        transition: opacity 0.2s linear;
        content: '';
        opacity: 0;
        width: 100%;
        height: 0;
        position: absolute;
        left: 11px;
        top: -19px;
        border-top: solid 1px @xlGrey;
        border-left: solid 1px @xlGrey;
        border-right: solid 1px @xlGrey;
        border-bottom: solid 1px #fff;
        background-color: #fff;
      }
      &:hover {
        .header__nav__link {
          z-index: 103;
        }
        &:before {
          z-index: 102;
          opacity: 1;
          height: 82px;
        }
        .header__nav__sub {
          margin: auto;
          height: auto;
          opacity: 1;
          pointer-events: auto;
          padding: 28px 45px 39px 10px;
        }
      }
      span {
        border-top: dashed 1px;
        border-bottom: dashed 1px;
        padding-top: 1px;
        padding-bottom: 1px;
        border-color: inherit;
      }
      &:after {
        content: '';
        width: 5px;
        height: 5px;
        border-radius: 50%;
        background: @lGrey;
        position: absolute;
        right: -10px;
        top: 50%;
        margin-top: -2px;
      }
    }
  }

}
.header__bottom {
  margin-bottom: 10px;
}