.catalog-filter {
  padding-right: 15px;
  &__reset {
    cursor: pointer;
    font-weight: normal;
    color: @lGrey;
    text-transform: none;
    font-size: 11px;
  }
  &__title {
    text-transform: uppercase;
    font-weight: bold;
    cursor: pointer;
    padding-bottom: 19px;
  }
  &__search {
    border-radius: 3px;
    border: solid 1px @xlGrey;
    height: 28px;
    width: 100%;
    margin-top: 20px;
    padding-left: 5px;
  }
  &__toggle {
    cursor: pointer;
    padding-left: 10px;
    transition: all .2s ease-in-out;
    &.hide {
      padding-left: 0;
      padding-right: 10px;
      transform: rotate(180deg);
    }
    .icon {
      font-size: .7em;
      vertical-align: 1px;
      fill: @lGrey;
    }
  }
  &__content {
    margin-top: 4px;
    li {
      margin-bottom: 16px;
    }
    border-bottom: solid 1px @xlGrey;
    padding-bottom: 22px;
    margin-bottom: 40px;
  }
  &__range {
    margin-bottom: 30px;
  }

  .noUi-inputs {
    margin-bottom: 15px;
    input {
      width: 84px;
      height: 28px;
      border-radius: 3px;
      border: solid 1px @xlGrey;
      padding-left: 5px;
    }
  }
}